//base
@import "base/normalise";
@import "base/fonts";
@import "base/vars";

body {
  font-family: 'Roboto', sans-serif;
  position: relative;
  overflow: hidden;
  img {
    position: absolute;
    width: 45%;
    height: auto;
    bottom: get-vw(-950px); }
  h1 {
    font-size: get-vw(72px);
    color: #31343b;
    padding-top: get-vw(50px);
    margin-bottom: get-vw(75px);
    font-weight: 700;
    span {
      color: #214dc3; } }
  h2 {
    font-size: get-vw(42px);
    padding-top: 11vw;
    color: #31343b;
    font-weight: 700;
    position: relative;
    span {
      color: #214dc3; } }
  p {
    color: #31343b;
    font-size: get-vw(28px);
    font-weight: 300;
    span {
      color: #214dc3; } }
  .text {
    width: 50%;
    float: right;
    position: relative; }

  .line {
    position: relative;
    top: get-vw(-30px);
    left: get-vw(-50px);
    transform: translate(-50%, -50%);
    height: get-vw(6px);
    width: get-vw(200px);
    background: linear-gradient(90deg, #fff 50%, #3962e2 50%); }

  .blue {
    position: absolute;
    left: get-vw(660px);
    width: get-vw(250px);
    height: get-vw(750px);
    background: #3962e2;
    z-index: -1; } }








@import "media/media";




